
.topicLists {
  font-size: 14px;
}
.topicLists .item {
  padding: .1rem 0 .1rem .1rem;
}
.topicLists img {
  /*vertical-align: middle;*/
  border: 0;
  border-radius: 3px;
}
.topicLists .item:nth-child(1) {
  border-top: none;
}
.topicLists .item {
  padding-right: .1rem;
  background: #fff;
  border-top: 1px solid #f0f0f0;
}
.author,
.replyCount {
  float: left;
}
.replyCount {
  width: .6rem;
  line-height: .28rem;
  display: inline-block;
  text-align: center;
  font-size: 14px;
}
.countReplies {
  color: #9e78c0
}
.countVisits {
  font-size: 10px;
  color: #b4b4b4;
}
.author img {
  width: .26rem;
  height: .26rem;
}
.latestReply {
  float: right;
}
.latestReply img {
  width: .18rem;
  height: .18rem;
  margin-right: .1rem;
}
.titleWrap {
  line-height: .26rem;
}
.putTop {
  background: #80bd01;
  color: #fff;
}
.putTop,
.nomalTab {
  padding: .02rem .04rem;
  font-size: 12px;
  border-radius: 3px;
}
.nomalTab {
  background-color: #e5e5e5;
  color: #999;
}
a.topicTitle {
  max-width: 60%;
  color: #333;
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
}
a.topicTitle:hover {
  text-decoration: underline;
}
a.topicTitle:visit {
  color: #888;
}
.latestReply .replyTime {
  display: inline-block;
  text-align: center;
  color: #778087;
  font-size: 11px;
  min-width: .3rem;
  line-height: .26rem;
}



