
















































































































































	
/*公共样式*/
.contentWrap {
	width: 90%;
	max-width: 14rem;
	/*min-width: 9.6rem;*/
	margin: .15rem auto;
	min-height: 4rem;
}
.sidebar {
	width: 2.8rem;
	font-size: .14rem;
	float: right;
	margin-bottom: .2rem;
	/*background: #fff;*/
}
.content {
	/*background: #fff;*/
	padding: 0;
	margin-right: 3.05rem;
}
.panel {
	margin-bottom: .13rem;
}
.titleHeader {
	color: #444;
	font-size: 13px;
	padding: .1rem;
	background-color: #f6f6f6;
	border-radius: 3px 3px 0 0;
}
.innerContent {
	background: #fff;
	padding: .1rem;
	border-radius: 0 0 3px 3px;
}
@media screen and (max-width: 978px) {
.content {
		margin: 0;
}
.others {
		display: none;
}
.footer {
		display: none;
}
a.topicTitle {
		max-width: 50% !important;
}
}
@media screen and (max-width: 1124px) {
a.topicTitle {
		max-width: 36% !important;
}
}
@media screen and (min-width: 1126px) and (max-width: 1296px) {
a.topicTitle {
		max-width: 50% !important;
}
}



